<template>
	<uni-popup ref="pickupTimePopup" type="bottom">
		<view class="popup-content">
			<view class="popup-header">
				<text class="popup-title">期望上门时间</text>
				<view class="popup-close" @tap="close">
					<uni-icons type="closeempty" size="22" color="#666666"></uni-icons>
				</view>
			</view>
			
			<view class="pickup-content">
				<view class="pickup-notice">
					<text class="notice-text">请至少提前半小时预约上门时间，若是紧急件请与快递员沟通，实际上门时间以协商后的为准。</text>
				</view>
				
				<view class="time-section">
					<!-- 左侧天数选择 -->
					<view class="days-column">
						<view 
							class="day-item" 
							:class="{ active: selectedDay === 'today' }" 
							@tap="selectDay('today')"
							v-if="!afterSixPM"
						>
							<text class="day-text">今天</text>
						</view>
						<view 
							class="day-item" 
							:class="{ active: selectedDay === 'tomorrow' }" 
							@tap="selectDay('tomorrow')"
						>
							<text class="day-text">明天</text>
						</view>
						<view 
							class="day-item" 
							:class="{ active: selectedDay === 'dayAfterTomorrow' }" 
							@tap="selectDay('dayAfterTomorrow')"
							v-if="afterSixPM"
						>
							<text class="day-text">后天</text>
						</view>
					</view>
					
					<!-- 右侧时间段选择 -->
					<view class="time-slots-column">
						<view 
							class="time-slot" 
							:class="{ active: selectedTimeSlot === '08:00-12:00' }"
							@tap="selectTimeSlot('08:00-12:00')"
						>
							08:00-12:00
						</view>
						<view 
							class="time-slot" 
							:class="{ active: selectedTimeSlot === '12:00-20:00' }"
							@tap="selectTimeSlot('12:00-20:00')"
						>
							12:00-20:00
						</view>
					</view>
				</view>
			</view>
			
			<view class="popup-footer">
				<view 
					class="pickup-button" 
					:class="{
						'confirm-btn': true,
						'confirm-btn-active': canConfirm,
						'pickup-button-disabled': !canConfirm
					}"
					@tap="confirm"
				>确定</view>
			</view>
		</view>
	</uni-popup>
</template>

<script>
export default {
	name: 'pickup-time-popup',
	data() {
		return {
			selectedDay: '',
			selectedTimeSlot: '',
			afterSixPM: false
		}
	},
	computed: {
		canConfirm() {
			return this.selectedDay && this.selectedTimeSlot
		}
	},
	methods: {
		open() {
			this.checkTime()
			this.$refs.pickupTimePopup.open()
		},
		close() {
			this.$refs.pickupTimePopup.close()
		},
		checkTime() {
			const now = new Date()
			const hour = now.getHours()
			this.afterSixPM = hour >= 18
			
			// 重置选择
			this.selectedDay = ''
			this.selectedTimeSlot = ''
			
			// 默认选中今天或明天
			if (this.afterSixPM) {
				this.selectDay('tomorrow')
			} else {
				this.selectDay('today')
			}
		},
		selectDay(day) {
			this.selectedDay = day
			this.selectedTimeSlot = '' // 清空时间段选择
		},
		selectTimeSlot(timeSlot) {
			this.selectedTimeSlot = timeSlot
		},
		confirm() {
			if (!this.canConfirm) return
			
			let dayText = ''
			if (this.selectedDay === 'today') dayText = '今天'
			else if (this.selectedDay === 'tomorrow') dayText = '明天'
			else if (this.selectedDay === 'dayAfterTomorrow') dayText = '后天'
			
			this.$emit('confirm', `${dayText}${this.selectedTimeSlot}`)
			this.close()
		}
	}
}
</script>

<style>
.popup-content {
	background-color: #FFFFFF;
	border-radius: 24rpx 24rpx 0 0;
	position: relative;
	z-index: 999;
	height: 50vh;
	display: flex;
	flex-direction: column;
}

.popup-header {
	padding: 30rpx;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1rpx solid #F5F5F5;
}

.popup-title {
	font-size: 32rpx;
	font-weight: bold;
	color: #333333;
}

.popup-close {
	padding: 10rpx;
}

.pickup-content {
	flex: 1;
	padding: 30rpx;
	overflow-y: auto;
}

.pickup-notice {
	padding: 20rpx;
	background-color: #F8F8F8;
	border-radius: 8rpx;
	margin-bottom: 30rpx;
}

.notice-text {
	font-size: 26rpx;
	color: #666666;
	line-height: 1.5;
}

.time-section {
	display: flex;
	margin-top: 30rpx;
}

.days-column {
	width: 200rpx;
	border-right: 1rpx solid #F5F5F5;
}

.day-item {
	height: 88rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0;
}

.day-item.active {
	background-color: #F8F8F8;
	color: #007AFF;
	font-weight: bold;
}

.time-slots-column {
	flex: 1;
	padding: 0 30rpx;
}

.time-slot {
	height: 88rpx;
	display: flex;
	align-items: center;
	font-size: 28rpx;
	color: #333333;
	padding: 0 20rpx;
	border-radius: 8rpx;
}

.time-slot.active {
	background-color: #E6F2FF;
	color: #007AFF;
	font-weight: bold;
}

.popup-footer {
	padding: 24rpx 30rpx calc(24rpx + env(safe-area-inset-bottom));
	border-top: 1rpx solid #F5F5F5;
	background-color: #FFFFFF;
}

.pickup-checkbox {
	transform: scale(0.7);
}

.pickup-input {
	flex: 1;
	height: 80rpx;
	font-size: 28rpx;
	color: #333333;
}

.pickup-button {
	background: linear-gradient(to right, #FF9500, #FF5E3A);
	color: #FFFFFF;
	font-size: 32rpx;
	font-weight: bold;
	height: 88rpx;
	line-height: 88rpx;
	border-radius: 44rpx;
	text-align: center;
}

.pickup-button-disabled {
	opacity: 0.6;
	pointer-events: none;
	background: linear-gradient(to right, #CCCCCC, #999999);
}

.day-text {
	font-size: 28rpx;
	color: #333333;
}

.day-item.active .day-text {
	color: #007AFF;
	font-weight: bold;
}
</style> 